import React, { useContext, useState } from 'react';
import { IconButton, SvgIcon } from '@material-ui/core';
import { ThemeContext } from '../themes/ThemeProvider';

const TopNavigationToggleDarMode = () => {
  const [selectedTheme, setSelectedTheme] = useState(
    localStorage.getItem("theme") || "LightTheme"
  );
  const setThemeName = useContext(ThemeContext);

  const handleThemeChange = () => {
    selectedTheme === "lightTheme"
      ? setSelectedTheme("darkTheme")
      : setSelectedTheme("lightTheme");
  };

  React.useEffect(() => {
    setThemeName(selectedTheme);
  });
  return (
    <ThemeContext.Consumer>
    {() => (
      <IconButton 
          disableFocusRipple
          size='medium'
          color='primary.main'
          aria-label='toggle between dark and light modes'
          onClick={handleThemeChange}
        >
        <SvgIcon>
          <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24"><path d="M0 0h24v24H0z" fill="none"/><path d="M20 8.69V4h-4.69L12 .69 8.69 4H4v4.69L.69 12 4 15.31V20h4.69L12 23.31 15.31 20H20v-4.69L23.31 12 20 8.69zM12 18c-.89 0-1.74-.2-2.5-.55C11.56 16.5 13 14.42 13 12s-1.44-4.5-3.5-5.45C10.26 6.2 11.11 6 12 6c3.31 0 6 2.69 6 6s-2.69 6-6 6z"/></svg>
        </SvgIcon>
      </IconButton>
    )}
    </ThemeContext.Consumer>
  )
}


export default TopNavigationToggleDarMode
